<template>
  <el-card shadow="never">
    <el-row>
      <el-col :span="12">
        <!-- name -->
        <label>{{ header['name'] }}</label>
        <div>{{ counter[header['name']] }}</div>
      </el-col>
      <el-col :span="12">
        <!-- processing -->
        <label>{{ header['processing'] }}</label>
        <div>{{ counter[header['processing']] || 'idle' }}</div>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <!-- processed -->
        <label>{{ header['processed'] }}</label>
        <div class="processed-val">{{ counter[header['processed']].join(', ') }}</div>
      </el-col>
    </el-row>
  </el-card>
</template>

<script>
export default {
  props: {
    header: {
      type: Array,
      required: true
    },
    counter: {
      type: Object,
      required: true
    }
  },
  watch: {
    counter: {
      handler () {
        this.$emit('handleWatch')
      },
      deep: true
    }
  }
}
</script>

<style scoped>
.processed-val {
  min-height: 1rem;
  width: 100%;
  word-break: keep-all;
}
.el-row .el-col:not(:last-child) {
  margin: 0 0 1rem;
}
</style>