<template>
  <div id="app" style="display:flex;justify-content:center;margin:20px 0;">
    <el-container style="max-width:767px;">
      <el-header>
        <el-row
          style="height:inherit;"
          type="flex"
          justify="space-between"
          align="middle">
          <!-- waitings -->
          <el-col
            :span="12">
            waitings: {{ waitList.length }}
          </el-col>
          <!-- NEXT -->
          <el-col
            :span="12">
            <el-button
              style="width:100%;"
              size="mini"
              type="primary"
              align="middle"
              plain
              @click="handleNext">
              NEXT {{ nextNum }}
            </el-button>
          </el-col>
        </el-row>
      </el-header>
      <!-- All counters -->
      <el-main>
        <Counter
          v-for="(counter, idx) in counterList"
          :key="'counter' + idx"
          :header="header"
          :counter="counter"
          @handleWatch="handleWatch" />
      </el-main>
    </el-container>
  </div>
</template>

<script>
import Counter from '@/components/Counter'

export default {
  name: 'App',
  components: {
    Counter
  },
  data () {
    return {
      nextNum: 1,
      waitList: [],
      header: {
        name: 'name',
        processing: 'processing',
        processed: 'processed'
      },
      counters: ['Amy', 'Bob', 'Cory', 'Dora'],
      counterList: []
    }
  },
  methods: {
    initCounterList () {
      this.counters.forEach(name => {
        const objCounter = {}
        objCounter[this.header['name']] = name // name
        objCounter[this.header['processing']] = null // processing
        objCounter[this.header['processed']] = [] // processed
        this.counterList.push(objCounter)
      })
    },
    handleNext () {
      this.waitList.push(this.nextNum++)
    },
    hasIdle () {
      return this.counterList.findIndex(item => {
        return item[this.header['processing']] === null
      })
    },
    handleWatch () {
      const idleIdx = this.hasIdle()
      if (
        idleIdx !== -1 &&
        this.waitList.length !== 0) {
        this.counterList[idleIdx][this.header['processing']] = this.waitList.shift()
        this.handleProcessing(idleIdx)
      }
    },
    handleProcessing (idx) {
      setTimeout(() => {
        this.counterList[idx][this.header['processed']].push(this.counterList[idx][this.header['processing']])
        this.counterList[idx][this.header['processing']] = null
      }, this.randomTime())
    },
    randomTime () {
      return Math.floor(500 + Math.random() * 1000)
    }
  },
  created () {
    this.initCounterList()
  },
  watch: {
    waitList () {
      this.handleWatch()
    }
  }
}
</script>

<style scoped>
.el-main {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
</style>
